.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #000;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        width: 138.75em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 6.25em;
        display: inline-block;
        background-image: url(../img/modal-bg.webp);
        background-size: cover;
        background-position: center;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        position: fixed;
        top: 17.5em;
        right: 17.5em;
    }
}

.close-btn{
    width: 6.25em;
    @include center;
    img{
        width: 100%;
    }
}



