.deed{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,37.5em);
    gap: 7.5em;
    justify-content: space-between;
  }
}

.deed-card{
  text-align: center;
  &__img{
    @include imgCover(100%);
    margin-bottom: 2.5em;
  }
  &__info{
    margin-top: 1.5em;
  }
  &__btn{
    &:hover{
      opacity: .8;
    }
  }
  &__text{
    display: none;
  }
}

.border{
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  overflow: hidden;
  span{
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
  }
  ._bottom,
  ._top{
    left: 0;
    width: 100%;
    height: 15.5em;
    background-size: auto 100%;
  }
  ._left,
  ._right{
    height: 100%;
    width: 15em;
    background-size: 100% auto;
  }
  ._top{
    background-image: url(../img/top.webp);
    top: -2em;
  }
  ._bottom{
    background-image: url(../img/bottom.webp);
    bottom: -2.5em;
  }
  ._left{
    background-image: url(../img/left.webp);
    left: -1em;
  }
  ._right{
    background-image: url(../img/right.webp);
    right: -1.5em;
  }
  ._top-left,
  ._top-right,
  ._bottom-right,
  ._bottom-left{
    width: calc(29.875em * 1.2);
    height: calc(20.3125em * 1.2);
    background-size: cover;
  }
  ._top-left{
    background-image: url(../img/top-left.webp);
    top: 0;
    left: -2.25em;
  }
  ._top-right{
    background-image: url(../img/top-right.webp);
    top: 0;
    right: -2.25em;
  }
  ._bottom-right{
    background-image: url(../img/bottom-right.webp);
    bottom: 0;
    right: -2.25em;
  }
  ._bottom-left{
    background-image: url(../img/bottom-left.webp);
    bottom: 0;
    left: -2.25em;
  }
}
