.memory{
  &__items{
    margin-top: 6.25em;
    @include grid100gap(6.25em);
  }
}

.memory-card{
  display: grid;
  grid-template-columns: 83.75em calc(100% - 91.25em);
  gap: 7.5em;
  align-items: flex-start;
  &__img{
    width: 100%;
    display: block;
  }
  &__col2{
    @include grid100gap(2.5em);
  }
  &:nth-child(even){
    grid-template-columns: calc(100% - 91.25em) 83.75em;
    .memory-card__col2{
      order: -1;
    }
  }
}
