.history{
  padding-top: 6.25em;
  &__items{
    @include grid100gap(15.625em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 61.125em;
      transform: translateX(-50%);
      top: 0;
      width: 1em;
      height: 100%;
      background-color: #76493A;
      border-radius: 2em;
    }
  }
}

.history-card{
  display: grid;
  grid-template-columns: 50em 124em;
  justify-content: space-between;
  &__info{
    @include grid100gap(1.5em);
    padding-top: 6.25em;
    position: sticky;
    top: 0;
    &:before{
      content: '';
      position: absolute;
      left: 61.125em;
      transform: translateX(-50%);
      top: 8.25em;
      width: 3.5em;
      height: 3.5em;
      background-color: #76493A;
      border-radius: 50%;
    }
  }
  &__img{
    margin-bottom: 6.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    font-size: 3em;
    line-height: 125%;
    font-family: $font3;
  }
}

.nav{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
  z-index: 4;
  height: 13em;
  align-items: center;
  &__link{
    color: #D6B37E;
    display: inline-flex;
    margin: 0 auto;
  }
}
