.main{
  &._show-step2{
    .main-view1{
      display: none;
    }
    .main-view2{
      display: block;
    }
  }
}

.main-slider{
  .swiper-container{
    width: calc(100% + 2.5em);
  }
  .swiper-slide{
    padding-right: 2.5em;
    width: auto;
  }
  &__img{
    height: 43.75em;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
}

.custom-btn{
  width: 35em;
  position: relative;
  display: block;
  color: $yellow;
  font-family: $font2;
  text-transform: uppercase;
  &:hover{
    color: darken($yellow,10%);
  }
  &:before{
    content: '';
    padding-bottom: 54.6%;
    display: block;
    background-image: url(../img/btn-bg.webp);
    background-size: cover;
    background-position: center;
  }
  span{
    @include centerAbsolute;
    font-size: 3em;
    line-height: 125%;
    margin-top: .125em;
    width: 100%;
    text-align: center;
  }
  &--star{
    width: 33.625em;
    &:before{
      background-image: url(../img/btn-bg2.webp);
      padding-bottom: 91.4%;
    }
    span{
      font-size: 2.5em;
      margin-top: .625em;
    }
  }
}

.main-view{
  margin-top: 5em;
  &__nav{
    margin-top: 6.875em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    font-size: 1.21em;
  }
  &__info{
    margin-top: 2em;
    display: flex;
    gap: 7.5em;
  }
  &__nav2{
    display: flex;
    align-items: flex-end;
    margin-top: -1.375em;
    gap: 5.83em;
    .custom-btn--star{
      margin-left: auto;
    }
  }
}

.main-view2{
  display: none;
}
