.winners{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 4.0625em));
    gap: 6.25em 8.125em;
    margin-top: 6.25em;
  }
}

.winner-card{
  display: grid;
  grid-template-columns: 35em calc(100% - 41.25em);
  gap: 6.25em;
  &__col2{
    padding-top: 1.25em;
  }
  &__img{
    @include imgCover(125%);
  }
  &__btn{
    margin-top: 2em;
    span{
      border-bottom: 1px solid $black;
    }
  }
  &__text{
    display: none;
  }
}
