.path{
  color: #D7BA94;
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 17.5em 22em;
    pointer-events: none;
    z-index: 3;
    max-width: 165em;
  }
  &__map{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    padding: 10em 13.5em;
    img{
      width: 100%;
      min-height: 100%;
      display: block;
      object-fit: cover;
    }
    &:after{
      background-image: url(../img/grad.svg);
      opacity: 1;
      background-color: transparent;
    }
  }
}

.path-point{
  position: absolute;
  text-align: center;
  cursor: pointer;
  &__img{
    width: 16.33656em;
    position: absolute;
  }
  &:nth-child(1){
    top: 50em;
    left: 38em;
    .path-point__img{
      top: -7.81em;
      left: -9em;
    }
  }
  &:nth-child(2){
    top: 82em;
    left: 61em;
    .path-point__img{
      bottom: 100%;
      margin-bottom: 1.25em;
      left: 50%;
      transform: translateX(-50%);
      width: 12.25em;
    }
  }
  &:nth-child(3){
    top: 58em;
    left: 83em;
    .path-point__img{
      top: 7.625em;
      left: 6.75em;
      width: 21.5em;
    }
  }
  &:nth-child(4){
    top: 83em;
    left: 112em;
    .path-point__img{
      bottom: 7em;
      left: -4.25em;
      width: 40.25em;
    }
  }
  &:nth-child(5){
    top: 47em;
    left: 124em;
    .path-point__img{
      top: -5.25em;
      left: -2.75em;
      width: 50.875em;
    }
  }
  &:nth-child(6){
    top: 71em;
    left: 155em;
    .path-point__img{
      bottom: -8.5em;
      left: -27em;
      width: 42.5em;
    }
  }
  &:nth-child(7){
    top: 39em;
    left: 171em;
    .path-point__img{
      top: 8.25em;
      right: 6em;
      width: 39.5em;
    }
  }
  &__text{
    display: none;
  }
}
